.contact-detail {
  display: flex;
  justify-content: center;
  gap: 20px;

  .personal {
    display: flex;
    flex-direction: column;
    align-self: center;
  }
  img {
    border-radius: 50%;
    height: 25vh;
    width: 25vh;
    background-color: #fff;
  }
}

.btns {
  display: flex;
  justify-content: center;
  margin: 25px;
  gap: 10px;
  & > * {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #405cf5;
    border-radius: 6px;
    border-width: 0;
    box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset, rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
    //   box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    text-align: center;
    height: 40px;
    width: 100px;
  }
}
